import { convertToInternationalCurrencySystem } from "../../utils/utils";
import dateFormat from 'dateformat';
import styled from "styled-components";
import UnderlineText from "../UnderlineText/UnderlineText";

const Wrapper = styled.div`
&.custom-tooltip {
  padding: 0 0 0 10px;
  color: #E0ECFF;
  z-index: 2;
  .tip-title {
    font-size: 16px;
    color: #E0ECFF;
  }
  .value {
    font-size: 32px;
    font-weight : 700;
    font-family : Barlow;
  }
  .date {
    margin-top: 4px;
    margin-left: 4px;
    font-size: 14px;
    height: 14px;
  }
}
`

const RechartTooltip = ({ title, active, payload, label, lastItem, decimalScale = 2, tip }) => {
  if (active && payload && payload.length) {
    return (
      <Wrapper className="custom-tooltip">
        <div className='tip-title'>{title}</div>
        <div className="value">
          {payload[0].payload.name && payload[0].payload.name}
          {tip ? <UnderlineText text={convertToInternationalCurrencySystem(payload[0].payload.value, decimalScale)} tip={tip}  />: convertToInternationalCurrencySystem(payload[0].payload.value, decimalScale)}
        </div>
        <div className="date">{payload[0].payload.time && `${dateFormat(new Date(payload[0].payload.time * 1000), 'yyyy-mm-dd')}(UTC)`}</div>
      </Wrapper>
    );
  } else if (title && lastItem.value) {
    return (
      <Wrapper className="custom-tooltip">
        <div className='tip-title'>{title}</div>
        <div className="value"> {lastItem.name && lastItem.name} {tip ? <UnderlineText tip={tip} width='328' text={convertToInternationalCurrencySystem(lastItem.value, decimalScale)} /> : convertToInternationalCurrencySystem(lastItem.value, decimalScale)}
        </div>
        <div className="date">{lastItem.timestamp && `${dateFormat(new Date(lastItem.timestamp * 1000), 'yyyy-mm-dd')}(UTC)`}</div>
      </Wrapper>
    );
  }
  return null
};
export default RechartTooltip